<template>
  <form action class="filter">
    <input @input="filter" type="text" placeholder="search:" v-model="filterBy.searchStr" />
    <input @input="filter" type="number" placeholder="From:"  v-model="filterBy.fromPrice" />
    <input @input="filter" type="number" placeholder="To:"  v-model="filterBy.toPrice" />
  </form>
</template>

<script>
export default {
  name: "book-filter",
  data() {
    return {
      filterBy: {
        searchStr: "",
        fromPrice: null,
        toPrice: Infinity
      }
    };
  },
  methods: {
    filter() {
      this.$emit("filter", { ...this.filterBy });
    }
  }
};
</script>