<template>
  <form @submit.prevent="onSubmit" class="reviews-form flex" v-if="!isReviewd">
    <div class="flex column space-between inputs">
      <input placeholder="Name:" type="text" name="name" id="name" v-model="review.name" />            <input placeholder="Rate:" type="number" name="rate" id="rate" min="1" max="5" v-model="review.rate" />
      Read At:
      <input placeholder="Read At:" type="date" name="readAt" id="date" v-model="review.readAt" />
    </div>
    <textarea placeholder="Review:" rows="6" name="reviewContext" id="review" v-model="review.reviewContext" />
    <button>Submit</button>
  </form>
  <div class="thanks" v-else>
    Thank for reviewing this book!

  </div>
</template>

<script>
export default {
  name: "review-add",
  data() {
    const date = new Date();
    return {
      review: {
        name: "",
        readAt: `${date.getFullYear()}-${date.getMonth() +
          1}-${date.getUTCDate()}`,
        rate: "-",
        reviewContext: ""
      },
      isReviewd:false
    };
  },

  methods: {
    onSubmit() {
      this.$emit("add-review", { ...this.review });
      this.isReviewd=true;
      
    }
  }
};
</script>

