<template>
  <section class="header-container grid-container">
    <div class="header flex align-center space-between "  v-bind:class="{ hide: isHide}">
          <router-link to="/">
      <img src="../assets/book-logo.png" alt="">
      </router-link>
      <ul class="nav clean-list flex">
        <li>
          <router-link to="/">Home</router-link>
        </li>
        <li>
          <router-link to="/missbooks">Books</router-link>
        </li>
        <li>
          <router-link to="/about">About</router-link>
        </li>
      </ul>
    </div>
  </section>
</template>

<script>
export default {
      data() {
    return {
      isHide: false
    };
  },
  created() {
   this.hideShow()
  },
  watch: {
    "$route"() {
   this.hideShow()
    }
  },
  methods:{
      hideShow(){
          if(this.$route.path==='/') this.isHide=true;
          else this.isHide=false
      }
  }
};
</script>

<style lang="scss">
.header-container {
  background-color: #1a2a42;
}

.header {
  font-family: monospace;
  color: rgb(255, 255, 255);
  h1 {
    margin: 0;
    font-size: 2rem;
  }
  .nav {
    li:not(:last-child) {
      margin-inline-end: 1rem;
    }
    a {
      text-decoration: none;
      color: rgb(255, 255, 255);
      font-size: 1.1rem;
      line-height: 5rem;
    }
    .router-link-exact-active {
      color: #8d93a5;
    }
  }
  img{
      width: 7rem;
  }
}
.hide{
    display: none !important;
}
</style>