<template>
    <ul  class="books-list clean-list   ">
        <li v-for="(book, idx) in books" :key="idx">
            <book-preview :book="book" @click.native="onSeeDetails(book.id)"/>
   
        </li>
    </ul>
    
</template>

<script>

    import bookPreview from './book-preview.vue'; 

    export default {
        name: 'book-list',
        props: ['books'],
        methods: {
        
            remove(id) {
                this.$emit('remove', id);
            },
            onSeeDetails(id){
                
              this.$router.push(`missbooks/${id}`);;
                
            }
        },
        components: {
            bookPreview
        }
    }
</script>



