<template>
  <section class="book flex column align-center">
    <div class="img-container">
      <img :src="book.thumbnail" alt />
    </div>
    <h4 class="title ">{{book.title}}</h4>
    <h5 class="author">Author:{{book.authors[0]}}</h5>
    <p>Price:{{book.listPrice.amount }}{{ getCurrency}}</p>
  </section>
</template>

<script>
export default {
  name: "book-preview",
  props: ["book"],

  computed: {
    getCurrency() {
      switch (this.book.listPrice.currencyCode) {
        case "EUR":
          return "€";
        case "ILS":
          return "₪";
        case "USD":
          return "$";
      }
    }
  }
};
</script>