<template >
  <transition name="fade">
    <section class="book-details" v-if="book">
      <div class="book-container flex wrap">
        <div class="img-container">
          <img :src="book.thumbnail" alt />
        </div>
        <button @click="close()" class="exit">&#10006;</button>
        <div class="details-container flex column align-baseline">
          <h3>{{book.title}}</h3>
          <p>
            <span class="key">Author:</span>
            {{ book.authors[0]}}
          </p>
          <p class="description">
            <span class="key">Description:</span>
            <span v-if="showMoreTxt">
              <long-text v-bind:txt="book.description" />
              <span @click="toggleTxt" class="less">Show Less</span>
            </span>
            <span v-else>
              {{book.description.slice(0, 20)}}
              <span @click="toggleTxt" class="more">...Show More</span>
            </span>
          </p>
          <p>{{getReadType}}</p>
          <p>{{getSeniority}}</p>
          <p>Sale!</p>
          <p>
            <span class="key">Price:</span>
            <span
              v-bind:class="{ expensive: isExpensive, cheap: isCheap }"
            >{{book.listPrice.amount}}</span>
          </p>
          <p class="key">Reviews:</p>
          <router-link class="prev" :to="'/missbooks/'+prevNext.prevId" v-if="prevNext.prevId">&lt;</router-link>

          <router-link class="next" :to="'/missbooks/'+prevNext.nextId" v-if="prevNext.nextId">&gt;</router-link>

          <div class="reviews">
            <div class="review" v-for="(review,idx) in book.reviews" :key="idx">
              <p>
                <span class="key">Name:</span>
                {{review.name}}
              </p>
              <p>
                <span class="key">Read At:</span>
                {{review.readAt}}
              </p>
              <p>
                <span class="key">Rate:</span>
                {{review.rate}}
              </p>
              <p>
                <span class="key">Review:</span>
                {{review.reviewContext}}
              </p>
            </div>
          </div>
        </div>
        <review-add @add-review="addReview" />
      </div>
    </section>
  </transition>
</template>
      

<script>
import bookService from "../services/bookService.js";
import longText from "../components/long-text.vue";
import reviewAdd from "../components/review-add.vue";

export default {
  name: "book-details",
  data() {
    return {
      book: null,
      isExpensive: null,
      isCheap: null,
      showMoreTxt: false
    };
  },
  async created() {
    this.loadBook();

  },
  methods: {
    close() {
      this.$router.push(`/missbooks`);
    },
    toggleTxt() {
      this.showMoreTxt = !this.showMoreTxt;
    },
    async addReview(review) {
      if (this.book.reviews) {
        this.book.reviews.push(review);
        this.book = await this.$store.dispatch({
          type: "saveBook",
          book: this.book
        });
      } else {
        this.book["reviews"] = [review];
        this.book = await this.$store.dispatch({
          type: "saveBook",
          book: this.book
        });
      }
    },
    async loadBook() {
      this.book = await this.$store.dispatch({
        type: "getBookById",
        id: this.$route.params.id
      });
      this.isExpensive = this.book.listPrice.amount > 150 ? true : false;
      this.isCheap = this.book.listPrice.amount < 20 ? true : false;
      this.prevNext = this.$store.getters.getPrevNext;
    }
  },
  computed: {
    getReadType() {
      if (this.book.pageCount > 500) return "Long reading";
      if (this.book.pageCount > 200) return "Decent reading";
      if (this.book.pageCount < 100) return "Light reading";
    },
    getSeniority() {
      const date = new Date();
      if (date.getFullYear() - this.book.publishedDate > 10)
        return "Veteran Book";
      if (date.getFullYear() - this.book.publishedDate < 1) return "New!";
    }
  },
  watch: {
    "$route.params.id"() {
      this.loadBook();
    }
  },
  components: {
    longText,
    reviewAdd
  }
};
</script>



